/* eslint-disable */

import { useParams, Link, useNavigate, json } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import React from 'react';
import { Nav, Button } from 'react-bootstrap';
import { Context1 } from './../App.js'
import { useDispatch, useSelector } from "react-redux";
import { addItem } from './../store.js';


function Detail(props) {

  //let state = useSelector((state) => { return state })


  let { 재고 } = useContext(Context1);
  let dispathch = useDispatch()
  let navigate = useNavigate();

  // useEffect 사용법 정리
  // 1. useEffect(()=> {})   재랜더링마다 코드실행하고 싶으면 사용
  // 2. useEffect(()=> {},[]) mount시 1회 코드 실행하고 싶으면 사용
  // 3. useEffect(()=> { return () => { 코드 }},[]) ummount시 1회 코드 실행하고 싶으면 사용


  //  let [count, setCount] = useState(0);
  let [입력값, 입력값변경] = useState('');
  let { id } = useParams();
  // let 찾은상품 = props.shoes.find(function (x) {
  //   console.log("찾은 상품은 = " + id);
  //   return x.id == id
  // });

   let 찾은상품 = props.shoes.find(x => x.id == id);

  let [alert, setAlert] = useState(true)
  //let [alert2, setAlert2] = useState(true)
  let [탭, 탭변경] = useState(0);

  


  useEffect(() => {
    let time = setTimeout(() => {
      setAlert(false)
    }, 3000)
    return () => {
      console.log('먼저 time을 클린하게 해준 후 위에 로직진행')
      clearTimeout(time)
    }
  }, [])

  let [fade2, SetFade2] = useState('');

  useEffect(() => {
    setTimeout(() => SetFade2('end'), 100);
    // 위에 있는 미세한 시간차를 준것은 저렇게 안하면 위에가 안먹힘.
    return () => {
      SetFade2('')
    }
  }, [])

  useEffect(()=>{
    
    let 꺼낸거 = localStorage.getItem('data');

    localStorage.setItem('data', JSON.stringify(찾은상품))
  }, [])

  

  return (
    <div className="container">
      {alert == true ?
        <div className="alert alert-warning">
          <h4>20% 할인 진행 중 입니다.</h4>
        </div>
        : null
      }

      <h4>현재 재고는 {재고[찾은상품.id]} 개 입니다.</h4>

      {/* <button onClick={()=> {setCount(count+1)}}>버튼</button> */}
      <div className="row">

        <div className={'start ' + fade2}>
          <div className="title">
            {/* <div className="col-md-6"> */}
            {/* <input onChange={(e) => {
              if (isNaN(e.target.value) == true)
                setAlert2(false);
              if (isNaN(e.target.value) == false)
                입력값변경(e.target.value)
            }} />
            {alert2 == false ?
              <div className="alert alert-warning">
                <h4>한글 쓰지 마시오</h4>
              </div>
              : null
            }
            <p>입력값은 : {입력값}</p> */}
            <img className="imgcontrol" src={'https://codingapple1.github.io/shop/shoes' + (찾은상품.id + 1) + '.jpg'} width="100%" />
            {/* </div>
          <div className="col-md-6"> */}

            <h4 className="pt-5">{props.shoes[찾은상품.id].title}</h4>
            <p>{props.shoes[찾은상품.id].content}</p>
            <p>{props.shoes[찾은상품.id].price}</p>

            

          

           
            <Link to="/cart/">
              <Button variant="outline-danger" onClick={() => {
                console.log("넘길 데이터 : " + props.shoes[찾은상품.id].id);
                // dispathch(addItem(props.shoes[찾은상품.id]))
                
                dispathch(addItem({ id: props.shoes[찾은상품.id].id, name: props.shoes[찾은상품.id].title, count: 1 }))
              }}>주문하기</Button>
            </Link>
            &nbsp;
            <Link onClick={() => { navigate(-1) }}>
              <Button variant="outline-secondary">나가기</Button>
            </Link>

            {/* </div> */}
          </div>

        </div>

      </div>

      <Nav variant="tabs" defaultActiveKey="link0">
        <Nav.Item>
          <Nav.Link onClick={() => { 탭변경(0) }} eventKey="link0">판매자</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link onClick={() => { 탭변경(1) }} eventKey="link1">상품 정보</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link onClick={() => { 탭변경(2) }} eventKey="link2">상품 리뷰</Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent 탭={탭} shoes={props.shoes[찾은상품.id]} />

    </div>

  )

}

function TabContent({ 탭, shoes }) {

  let [fade, SetFade] = useState('');
  let { 재고 } = useContext(Context1)

  useEffect(() => {
    setTimeout(() => SetFade('end'), 100);
    // 위에 있는 미세한 시간차를 준것은 저렇게 안하면 위에가 안먹힘.
    return () => {
      SetFade('')
    }
  }, [탭])


  return (
    <div className={'start ' + fade}>
      {[
        <div className="text-left">
          <h4>이노트리</h4>
          <p className="margin-top-control">가나다라마바사가나나다라마바사
            safsafsfsaf</p>
          <p className="margin-top-control">가나다라마바사가나나다라마바사</p>
          <p className="margin-top-control">가나다라마바사가나나다라마바사</p>

        </div>,

        <div>
          <h4>{shoes.title}</h4>
          <p>{shoes.content}</p>
        </div>,
        <div>{재고}</div>][탭]}
    </div>
  )

}
export default Detail;